@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap);
.container{width:100%;height:100%;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.container main{padding:2em;max-width:700px;width:100%;margin:0 auto}@media only screen and (min-width: 700px){.container main{display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(2, 1fr);grid-template-areas:'welcome welcome' 'subscription aboutus'}}.container main section{padding:1em}@media only screen and (min-width: 700px){.container main section{padding:2em}}.container main section h1{margin-bottom:1.2em;font-size:1.2em}.container main section h2{font-size:1em}.container main section p{font-size:0.9em}.container main section button{padding:0.8em;width:100%;border-radius:0.5em;border:1px solid transparent;box-shadow:0px 5px 10px 2px rgba(59,59,59,0.268);cursor:pointer}section:nth-child(1){background-color:#fff;border-radius:0.5em 0.5em 0 0;grid-area:welcome}@media only screen and (min-width: 700px){section:nth-child(1){-webkit-align-self:end;align-self:end}}section:nth-child(1) h1{color:#2ab2af}section:nth-child(1) h2{color:#c0df34;margin-bottom:1.2em}section:nth-child(1) p{color:#98a6bd;margin-top:0}section:nth-child(2){background-color:#2ab2af;color:#fff;grid-area:subscription}section:nth-child(2) h2{color:#e5eff5;font-weight:400;-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex}section:nth-child(2) h2 strong{font-size:2em;color:#fff;margin-right:0.2em}section:nth-child(2) p{margin-bottom:2em}section:nth-child(2) button{background-color:#c0df34;color:#fff;margin-bottom:1em}@media only screen and (min-width: 700px){section:nth-child(2) button{margin-bottom:0}}section:nth-child(3){background:#2ec2c0;color:#fff !important;grid-area:aboutus}section:nth-child(3) a{color:#e5eff5;text-decoration:none;font-size:0.8em}section:nth-child(3) ul{list-style:none;padding:0}section:nth-child(3) ul li{margin-bottom:0.5em;font-weight:400}*{box-sizing:border-box}html{font-family:'Karla', sans-serif;font-size:16px}body{font-size:100%;background-color:#e5eff5}p{line-height:32px}

