.container {
	width: 100%;
	height: 100%;
	// display: flex;
	justify-content: center;
	align-items: center;

	main {
		padding: 2em;
		max-width: 700px;
		width: 100%;
		/* grid-template-columns: 1fr;
		grid-template-rows: repeat(3, 1fr);
		border-radius: 2em; */
		margin: 0 auto;

		@media only screen and (min-width: $desktop) {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			grid-template-rows: repeat(2, 1fr);
			grid-template-areas:
				'welcome welcome'
				'subscription aboutus';
		}

		section {
			padding: 1em;

			@media only screen and (min-width: $desktop) {
				padding: 2em;
			}

			h1 {
				margin-bottom: 1.2em;
				font-size: 1.2em;
			}
			h2 {
				font-size: 1em;
			}
			p {
				font-size: 0.9em;
			}
			button {
				padding: 0.8em;
				width: 100%;
				border-radius: 0.5em;
				border: 1px solid transparent;
				box-shadow: 0px 5px 10px 2px hsla(0, 0%, 23%, 0.268);
				cursor: pointer;
			}
		}
	}
}
